body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-linear-gradient(bottom, #dfe9f3 0%, white 100%);
  background-image: linear-gradient(to top, #dfe9f3 0%, white 100%);
  height: 100%;
  overflow: auto;
}

html {
  overflow: hidden;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

#supercontainer {
  position: relative;
}

#toprow {
  background-color: rgb(70,69,94);
  border-bottom: rgb(104,110,113) solid 1px;
  width: 100%;
  height: 55px;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  box-shadow: 0 5px rgba(70, 69, 94, 0.4), 0 10px rgba(70, 69, 94, 0.3),
              0 15px rgba(70, 69, 94, 0.2), 0 20px rgba(70, 69, 94, 0.1),
              0 25px rgba(70, 69, 94, 0.05),
              -5px 5px 40px rgba(100, 0, 0, 0.4);

}

.select {
  flex: 1 0; /* explanation below */
  margin: 5px;
}

.name {
  color: rgb(87,208,182);
  display: inline;
  -webkit-transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
  transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.name:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

Select {
  display: inline-block;
  width: 100%;
}

#chartinfo {
  margin-top: 20px;
  display:inline-block;
  width: 42%;
  margin-left: 30px;
  background: white;
  box-shadow: 0 6px 15px rgba(36,37,38,0.08);
  border-radius: 16px;
  text-align: center;
  border-bottom: 4px solid rgb(242,242,242);
}

.chartinfotext {
  margin: 10px !important;
  display: inline;
  text-align: center;
}

.chartinfoTitle{
  text-align: center;
  display:inline-block;
  width: 95%
}

#questionmark{
    display: inline-block;
    cursor: pointer;
    width: 4%;
    margin-right: 2px;
    vertical-align: 180%;
    font-size: 20px;
    font-weight: bold;
    border: 1px solid rgb(52, 52, 52);
    border-radius: 30px;
    color: rgb(52, 52, 52)
}

.tooltiptextnew {
  visibility: hidden;
  width: 400px;
  background-color: rgb(111, 104, 122);
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  position: absolute;
  right: 10%;
  bottom: 22%
}

#questionmark:hover + .tooltiptextnew {
  visibility: visible;
}
#container {
    position: relative;
    width: 50%;
    display: inline-block;
}

svg {
    background: white;
    box-shadow: 0 6px 15px rgba(36,37,38,0.08);
    border-radius: 16px;
}

#legend {
    position: absolute;
    top: 10%;
    border-radius: 10px;
    left: 8%;
    height: 150px;
    width: 15px;
    background: #00ff00; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #00ff00 0%,#ffff00 50%,#ff0000 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #00ff00 0%,#ffff00 50%,#ff0000 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ff00', endColorstr='#ff0000',GradientType=0 ); /* IE6-9 */
}

.tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: rgb(111, 104, 122);
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
  
    /* Position the tooltip */
    position: absolute;
    top: 110%;
    left: 55%; 
    margin-left: -60px; 
    z-index: 1;
  }

  #legend:hover .tooltiptext {
    visibility: visible;
  }

#zero {
    position: relative;
    bottom: -35%;
    right: -115%;
}

#half {
    position: relative;
    bottom: -13%;
    right: -115%;
}

#all {
    position: relative;
    bottom: 15%;
    right: -115%;
}

.zoombuttons {
    text-align: center;
    margin-top: 10px;
    color: white;
    height: 35px;
    width: 35px;
    cursor: pointer;
    border-radius: 5px;
    background-color: rgb(111, 104, 122);
    font-size: 30px;
    text-decoration: none;
    -webkit-transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
    transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
  }

  .buttontext {
      margin: 0px;
      color: white;
      text-align: center;
      -webkit-user-select: none; /* Safari */
      -moz-user-select: none; /* Firefox */
      -ms-user-select: none; /* IE10+/Edge */
      user-select: none; /* Standard */
  }
  
  #buttoncontainer {
      position: absolute;
      top: 79%;
      left: 8%;
  }
  .zoombuttons:hover {
    opacity: 0.7;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
#containerchart {
    width: 42%;
    margin-left: 80px;
    display: inline-block;
    margin-right: 10px;
    height: auto;
    background: white;
    box-shadow: rgba(36, 37, 38, 0.08) 0px 6px 15px;
    border-radius: 16px;
}

#chartid {
    width: 100%; height: auto;
}

.axis {
    font-size: 15px;
}

.labels {
    font-size: 20px;
    font-weight: bold;
}

path { 
    stroke: steelblue;
    stroke-width: 2;
    fill: none;
}

.axis path,
.axis line {
    fill: none;
    stroke: grey;
    stroke-width: 1;
    shape-rendering: crispEdges;
}

circle {
    fill: steelblue;
}


div.tooltip {	
    position: absolute;		
    text-align: center;			
    width: 150px;					
    height: 70px;					
    padding: 2px;				
    font: 12px sans-serif;		
    background: lightsteelblue;	
    border: 0px;		
    border-radius: 8px;			
    pointer-events: none;			
}
#countrycontainer {
    width: 50%;
    margin-top: 20px;
    display:inline-block;
    margin-left: 50px;
    background: white;
    box-shadow: 0 6px 15px rgba(36,37,38,0.08);
    border-radius: 16px;
    text-align: center;
    border-bottom: 4px solid rgb(242,242,242);
}

.textcountry {
    margin: 10px !important;
    display: inline;
    text-align: center;
}

.countryTitle{
    text-align: center;
}
